import React from "react";
import { motion } from "framer-motion";

function Competencias() {
  return (
    <motion.section
      id="competencias"
      initial={{ opacity: 0, y: 30 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8 }}
      viewport={{ once: true }}
    >
      <h3>
        COMPETNCIAS <i className="fas fa-clipboard-list"></i>
      </h3>
      <ul>
        <li>
          <i className="fas fa-language mr-2"></i> Fluncia em ingls. Espanhol
          Bsico. Francs Bsico.
        </li>
        <li>
          <i className="fab fa-java mr-2"></i> Experincia em JAVA verses 6, 7,
          8, 11 e 17.
        </li>
        <li>
          <i className="fas fa-cogs mr-2"></i> Vivncia em programao reativa
          (Spring Webflux).
        </li>
        <li>
          <i className="fas fa-tasks mr-2"></i> Conhecimentos slidos em
          metodologias geis.
        </li>
        <li>
          <i className="fas fa-user-check mr-2"></i> Proativo, organizado,
          dinmico, comprometido e com tima habilidade de comunicao.
        </li>
        <li>
          <i className="fas fa-users-cog mr-2"></i> Experincia com coordenao
          e liderana de equipes.
        </li>
      </ul>
    </motion.section>
  );
}

export default Competencias;
